/* CSS reset */
* {
  font-variant-ligatures: none;
  font-feature-settings: "liga" 0;
  -webkit-tap-highlight-color: transparent;
}

/* no focus style for non-keyboard-inputs elements */
div:focus,
button:focus,
select:focus {
  outline: none;
}

h3 {
  font-weight: normal;
  margin: 0;
}

input:focus::placeholder, textarea:focus::placeholder {
  color: transparent;
}

body {
  background-color: var(--color-backgroundDefault);
  height: 100%;
  margin: 0px;
  overflow: hidden;
  width: 100%;
  font-family: var(--font-regular);
  color: var(--color-textPrimary);

  /* bringing WalletConnect in front of MPDialog, 1 more than that */
  --w3m-z-index: 1301;
}

body > iframe {
  display: none !important;
}

html {
  height: 100%;
}
