@keyframes shimmer {
  100% {background-position: left}
}

.productCardMediaContainer {
  position: relative;
}

.productCardOverlay {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
}

.productCardMediaContainer:hover > a > .productCardOverlay {
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.28) 0%,
    rgba(0, 0, 0, 0.74) 100%
  );
  cursor: pointer;
  opacity: 1;
}

.productCardMedia {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0px;
  gap: 10px;
  isolation: isolate;
  width: 320px;
  height: 400px;
  left: 382px;
  top: 34px;
  object-fit: cover;
  background: linear-gradient(-90deg,#0000 10%,#0005,#0000 70%) right/300% 100%;
  animation: shimmer 2.5s infinite;
  background-color: var(--color-backgroundDefault);
}

.productTitle {
  position: absolute;
  bottom: 12px;
  left: 15px;
  color: var(--color-commonWhite);
  z-index: 1;
  width: 90%;
  overflow: hidden;
}

.titleDimensionSpacing {
  margin-bottom: 6px;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
  text-align: left;
}

.productDimensions {
  color: var(--color-grey400);
}

.productBadge {
  display: inline-flex;
  flex-direction: row;
  padding: 8px 14px 8px 10px;
  gap: 5px;
  position: absolute;
  left: 16px;
  top: 16px;
  background: rgba(0, 0, 0, 0.28);
  border-radius: 16px;
  z-index: 1;
  height: 16px;
  min-width: 63px;
  align-items: center;
  backdrop-filter: blur(32px);
}

.badgeIcon {
  position: relative;
}

.badgeText {
  position: relative;
}

.productActionContainer {
  display: flex;
  flex-direction: row;
  padding: 0px;
  gap: 8px;
  position: absolute;
  width: 72px;
  height: 32px;
  right: 16px;
  top: 16px;
  justify-content: flex-end;
  z-index: 1;
}

.productLike {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 10px;
  width: 32px;
  height: 32px;
  /* Neutrals/Black Transparencies/28% */
  background: rgba(0, 0, 0, 0.28);
  /* Background Blur/64 */
  backdrop-filter: blur(32px);
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 16px;
  cursor: pointer;
  transition: opacity 500ms ease-in-out;
}

.productLike:hover {
  background: rgba(0, 0, 0, 0.60);
}


.productLikeIcon {
  position: relative;
  font-size: 17px;
  top: 3px;
  left: 0.5px;
  right: -0.5px;
}

.videoWrapper {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

@media (--mq-is-mobile) {
  .productCardMediaContainer, .productCardMedia {
    width: var(--mobilePCWidth);
    height: calc(1.2 * var(--mobilePCWidth));
  }
}
