@import "npm:@mp-frontend/core-components/public/index.css";

/* TODO go through and audit existing ps and make this the default */
p.reset {
  margin: 0px;
}

.breakWord {
  word-break: break-word;
}

.preserveWhiteSpace {
  white-space: pre;
}

.resetPointerEvents {
  pointer-events: auto;
}

.hidden {
  display: none;
}

.invisible {
  visibility: hidden;
  pointer-events: none;
}

#root {
  height: 100%;
}

.flexRowNoWrap {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.flexRowVA {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  align-items: center;
}

.flexColumn {
  display: flex;
  flex-direction: column;
  align-self: unset;
}

.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flexVCenter {
  display: flex;
  align-items: center;
}

.flexAbsCenter {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  flex-grow: 0;
}

.flexChildTextEllipsis {
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* See https://stackoverflow.com/questions/1833734/display-inline-block-extra-margin/8262649#8262649 */
.hideText {
  color: transparent;
  display: block;
  font-size: 0;
  line-height: 0;
}

.anchor {
  text-decoration: none;
}

.invisibleAnchor,
.defaultLink {
  color: inherit;
  text-decoration: none;
  transition: var(--transition-colors-smoothly);
}

.defaultLink:hover {
  text-decoration: underline;
}

.hoverableLink {
  text-decoration: none;
  transition: var(--transition-colors-smoothly);

  &:hover {
    color: var(--color-SolidNeutralGray3);
  }

  &:active {
    color: var(--color-SolidNeutralGray1);
  }
}

.buttonToLink {
  text-align: left;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  background: transparent;
  width: 100%;
}

.reenablePointerEvents {
  pointer-events: all;
}

.reset {
  margin: 0;
  padding: 0;
}

.MuiSvgIcon-root.MuiSvgIcon-fontSizeSmall {
  width: 14px;
  height: 14px;
}

.textOverflowEllipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

.visible {
  opacity: 1;
}

.invisible.invisible {
  opacity: 0;
  pointer-events: none;
}

.bold {
  font-family: var(--font-bold);
}

.bodyWithFooter {
  min-height: calc(100% - 300px);
}

.errorMsgColor {
  color: var(--color-errorMain);
}

.underline {
  text-decoration: underline;
}

.centerText {
  text-align: center;
}

.gridSpanRow {
  grid-column: 1 / -1;
}

/* TODO(framework) unify when default padding is in */
.defaultTextMargin {
  margin-top: 8px;
  margin-bottom: 8px;
}

.anchorV2 {
  color: var(--color-SolidNeutralGray5);
  text-decoration: underline;
}

.minContentWidth {
  width: min-content;
}

.max100Width {
  max-width: 100%;
}

.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.grecaptcha-badge {
  visibility: hidden;
}
