.artworkCardContainer {
  background: var(--color-commonWhite);
  padding: 32px;
  box-sizing: border-box;
  gap: 32px;
  justify-content: flex-start;

  @media screen and (min-width: 1920px) {
    gap: 24px;
  }

  @media (--mq-is-mobile) {
    gap: 20px;
    padding: 24px;
  }
}

.artworkCardSecondaryText {
  color: var(--color-SolidNeutralGray5);
}

.artworkCardFooterValue {
  width: 104px;

  @media (--mq-is-mobile) {
    width: 86px;
  }
}

.artworkCardContainer.inverted {
  background: var(--color-commonBlack);
  color: var(--color-commonWhite);

  & .artworkCardSecondaryText {
    color: var(--color-SolidNeutralGray2);
  }
}

.artworkAssetContainer {
  &.medium {
    height: 336px;

    @media screen and (min-width: 1920px) {
      height: 463px;
    }

    @media (--mq-is-mobile) {
      height: 267px;
    }
  }

  &.large {
    height: 388px;

    @media screen and (min-width: 1920px) {
      height: 507px;
    }

    @media (--mq-is-mobile) {
      height: 306px;
    }
  }
}

.artworkAsset {
  filter: drop-shadow(0px 8px 24px rgba(0, 0, 0, .16));
  transition: transform 300ms ease-out;

  &:hover {
    transform: scale(1.02);
  }
}

.artworkAssetMedia {
  max-width: 100%;
  max-height: 100%;
}

.artworkAssetEmbedded {
  height: 100%;
  object-fit: contain;
  width: 100%;

  @media (--mq-is-mobile) {
    min-height: unset;
  }
}
