.productCardContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  align-items: center;
  width: 320px;
  height: 508px;
  left: 384px;
  top: 192px;
  background-color: var(--color-commonWhite);
}

.productCardContainer:global(.inAuction) {
  background-color: var(--color-commonBlack);
}

.productPageContainer {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
}

.infoSection {
  width: 100%;
  padding: 0 12px;
  box-sizing: border-box;
}

@media (--mq-is-mobile) {
  .productCardContainer {
    --mobilePCWidth: calc(100vw - 48px);
    --mobilePCInfoHeight: 100px;
    width: var(--mobilePCWidth);
    height: calc(1.2 * var(--mobilePCWidth) + var(--mobilePCInfoHeight));
  }
}