.container {
  display: inline-block;
  position: relative;
  white-space: nowrap;
  width: max-content;
}

.popover {
  background-color: var(--color-commonWhite);
  box-shadow: 0px 8px 24px 0px rgba(0, 0, 0, 0.16);
  box-sizing: border-box;
  margin-top: 4px;
  min-width: 100%;
  padding: 16px;
  position: absolute;
  width: max-content;
  z-index: 1;

  &.alignLeft {
    left: 0;
  }

  &.alignRight {
    right: 0;
  }
}

.dialogContainer {
  align-items: flex-end;
}

.dialogPaper {
  height: fit-content;
  max-width: 100%;
}

.dialogContainer .dialogPaper .dialogContentRoot {
  padding: 16px 24px 24px;
}
