.productCardListingContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 8px;
}

.listingItem {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  align-items: flex-end;
}

.listingHeader {
  text-transform: capitalize;
  color: var(--color-SolidNeutralGray4);
  height: 16px;
  display: flex;
  align-items: center;
}

:global(.inAuction) .listingHeader {
  color: var(--color-SolidNeutralGray2);
}

.listingData {
  color: var(--color-primaryMain);
  height: 21px;
  margin-top: 2px;
}

:global(.inAuction) .listingData {
  color: var(--color-commonWhite);
}

.pinLeft {
  margin-left: 0;
}

.listingItem.pinLeft {
  align-items: flex-start;
}

.productCardAuctionContainer {
  display: flex;
  justify-content: end;
}
