.productCardDetailsContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 24px;
  padding: 12px 0px 0px 0px;
}

.productCardDetailsItem {
  display: flex;
  max-width: 50%;
}

.productCardDetailsItem:last-child {
  margin-left: auto;
}

.productCardDetailsLightTxt {
  color: var(--color-SolidNeutralGray4);
}

:global(.inAuction) .productCardDetailsLightTxt {
  color: var(--color-SolidNeutralGray2);
}

img.productCardProfileIcon {
  width: 28px;
  height: 28px;
  position: relative;
  margin-right: 5px;
}

.productCardProfileLink {
  color: var(--color-primaryMain);
  text-decoration: none;
  overflow: hidden;
  white-space: pre;
  text-overflow: ellipsis;
}

:global(.inAuction) .productCardProfileLink {
  color: var(--color-commonWhite);
}
