.oldGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 320px);
  justify-content: center;
  column-gap: 32px;
  row-gap: 32px;
  padding: 0 32px;
  width: calc(100% + 2 * var(--profile-pagePadding));
  box-sizing: border-box;
  position: relative;
  right: var(--profile-pagePadding);
}

@media (--mq-is-mobile) {
  .oldGrid {
    grid-template-columns: repeat(auto-fill, calc(100vw - 48px));
    column-gap: 8px;
    padding: 0;
    width: unset;
    position: unset;
    right: unset;
  }
}

.grid {
  box-sizing: border-box;
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  margin: 0 auto;
  max-width: 1440px;
  padding: 0;
  position: relative;

  @media screen and (min-width: 1920px) {
    max-width: 1920px;
  }

  @media (--mq-is-mobile) {
    grid-template-columns: repeat(auto-fill, 100%);
  }
}

.shiftUpOneRow {
  margin-top: -540px;
  pointer-events: none;
}

.artistCarouselContainer {
  box-sizing: border-box;
  margin: 0 auto 24px auto;
  max-width: 1440px;
  padding: 0 32px;
  position: relative;

  @media screen and (min-width: 1920px) {
    max-width: 1920px;
  }

  @media (--mq-is-mobile) {
    padding: 0;
    width: 100vw;
    margin-left: -24px;
  }
}
